import { Component, OnInit } from '@angular/core';
import {Course, Teacher} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {CourseService} from '../../course.service';
import {TeacherService} from '../../teacher.service';

@Component({
  selector: 'app-add',
  templateUrl: './course-add.component.html',
  styleUrls: ['./course-add.component.css']
})
export class CourseAddComponent implements OnInit {
  teachers: Teacher[];
  course: Course;
  teacher: Teacher;
  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService,
    private teacherService: TeacherService,
    private location: Location
  ) { }

  ngOnInit() { this.getTeachers() ; }
  // @ts-ignore
  compareFn(o1: Compare, o2: Compare): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  getTeachers(): void {
    this.teacherService.getTeachers ()
      .subscribe(res => this.teachers = res);
  }
  save(no: string, description: string, credit: string, teacher: Teacher, category: string, time: string, place: string): void {
    // @ts-ignore
    this.course = {id: null, no: no.trim(), description: description.trim(), credit: credit.trim(), teacher: this.teacher,
      category: category.trim(), time: time.trim(), place: place.trim(), countNum: null};
    if (!description || !no || !credit || !category || !teacher || !time || !place) { return; }
    this.courseService.addCourse(this.course)
      .subscribe(res => {alert(res.message); this.goBack(); });
  }

  goBack(): void {
    this.location.back();
  }

}
